@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400&display=swap");

body {
  background-color: #323031;
  display: flexbox;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: auto;
  font-family: "Montserrat", sans-serif;
}
.nave-btn {
  font-size: 1.25rem;
  color: yellow;
  background-color: black;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}
.nave-search {
  font-size: 1.25rem;
  color: yellow;
  margin-top: 1rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  background-color: black;
}

.nave-info-ul {
  font-size: 1.25rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  color: yellow;
  background-color: black;
  margin-top: 0.5rem;
  text-align: left;
}
.nave-info {
  margin-top: 0.5rem;
}
.sw-logo {
  float: left;
  align-items: left;
  justify-content: left;
  text-align: left;
}
svg {
  color: white;
  margin-left: 1rem;
}
.github {
  text-align: center;
  margin: auto;
  align-items: center;
  display: flex;
  justify-content: center;
  font-size: 1.5rem;
}
.gh-link {
  margin-left: -15rem;
}
